import React, { useState, useEffect } from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import { Box } from "@mui/material";
import { TextField } from "@mui/material";
import { Button } from "@material-ui/core";

import { useWeb3React } from "@web3-react/core";
import Modal from "../SharedComponents/Modal";
import Tokenlist from "components/SharedComponents/TokenList";
import { sendTip, approve, init } from "utils/contract";
import { contractAddress, currenices } from "constants/config";
import { ethAddress } from "constants/config";
import { convertUSDToEther } from "utils/utils";
import { strangerAddress } from "peer/peer";
import { sendTipToPartner } from "peer/socket";
import { useAppSelector } from "../../store";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function SendTip() {
  const { connected } = useAppSelector((state) => state.global);
  const [isOpen, setIsOpen] = useState(false);
  const [value, setValue] = React.useState("10");
  var { account, active, chainId } = useWeb3React();

  var selectedToken = {};

  const handleOpenModal = () => {
    let _isOpen = isOpen;
    setIsOpen(!_isOpen);
  };

  const handleRadioChange = (event) => {
    setValue(event.target.value);
  };

  const handleCustomChange = (event) => {
    let inputAmount = event.target.value;
    let lastChar = inputAmount.split("")[inputAmount.length - 1];
    if (lastChar >= "0" && lastChar < "9") {
      setValue(inputAmount.substr(1));
    } else if (lastChar === "$") setValue("");
  };

  const selectToken = (token) => {
    selectedToken = token;
  };

  const onSendTip = async () => {
    if (!active) {
      return;
    }
    handleOpenModal();
  };

  const confirm = async () => {
    if (!active) return;
    if (value === "") {
      toast.success("Please input tip amount correctly.");
      return;
    }

    handleOpenModal();
    init(chainId);

    selectedToken = currenices[chainId][0];
    let amount = value * 10 ** selectedToken.decimals;

    if (selectedToken.address !== ethAddress) {
      let approveTx = await approve(
        selectedToken.address,
        contractAddress,
        amount,
        account
      );
      if (approveTx == null) {
        toast.success(`You have to approve ${selectedToken.name}`);
        return;
      }
    } else amount = await convertUSDToEther(amount, chainId);

    let chatTx = await sendTip(
      selectedToken.address,
      strangerAddress,
      amount.toString(),
      account
    );

    if (chatTx !== null) {
      let success = sendTipToPartner({ amount: value });
      if (success) toast.success("Your tip has been sent!");
    } else {
      console.log("Tip canceled");
    }
  };

  return (
    <>
      <Button
        variant="outlinedSecondary"
        className="narrow-line-height"
        style={{
          color: "white",
          fontWeight: "700",
          fontSize: "1rem",
          marginRight: "0.3rem",
          opacity: active ? "1" : "0.5",
          cursor: active ? "pointer" : "default",
        }}
        onClick={onSendTip}
      >
        Send Tip
      </Button>
      <Modal isOpen={isOpen} onRequestClose={handleOpenModal} title="Send Tip">
        <Box>
          <FormControl sx={{ mt: 3, ml: 1, mr: 1 }} variant="standard">
            <FormLabel id="demo-error-radios" className="text-tip-amount">
              Tip Amount
            </FormLabel>
            <RadioGroup
              aria-labelledby="demo-error-radios"
              value={value}
              onChange={handleRadioChange}
            >
              <FormControlLabel value="10" control={<Radio />} label="$10" />
              <FormControlLabel value="25" control={<Radio />} label="$25" />
              <FormControlLabel value="50" control={<Radio />} label="$50" />
              <FormControlLabel value="100" control={<Radio />} label="$100" />
            </RadioGroup>
            <TextField
              id="outlined-number"
              label="Custom Amount (USD)"
              type="text"
              sx={{ mt: 2 }}
              InputLabelProps={{
                shrink: true,
              }}
              value={"$" + value}
              onChange={handleCustomChange}
            />
          </FormControl>

          <Tokenlist selectToken={selectToken} />

          <Box sx={{ display: "flex", justifyContent: "center", mt: 1, mr: 1 }}>
            {/* <Button
              sx={{ mt: 1, mr: 2 }}
              color="primary"
              variant="outlined"
              onClick={handleOpenModal}
            >
              Cancel
            </Button> */}

            <Button
              sx={{ mt: 1, mr: 1 }}
              variant="outlined"
              onClick={confirm}
              disabled={!connected}
            >
              Send
            </Button>
          </Box>
          <Box
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          >
            <p
              style={{
                marginBottom: "-20px",
                marginTop: "20px",
                fontSize: "10px",
                color: "grey",
              }}
            >
              Note: 4cam convenience fee is 40%
            </p>
          </Box>
        </Box>
      </Modal>
      <ToastContainer autoClose={7000} />
    </>
  );
}

export default SendTip;
