import React, { useEffect, useState } from "react";
import { Button } from "@material-ui/core";
import { useWeb3React } from "@web3-react/core";
import { toast } from "react-toastify";

const Microphone = ({ localStream, permission }) => {
  const [micEnabled, setMicEnabled] = useState(true);
  var { active } = useWeb3React();

  const handleToggleMic = () => {
    if (permission === false)
      toast.success(
        "Allow mic permission in your browser settings to toggle on/off."
      );
    if (localStream) {
      let toggledMic = !micEnabled;
      localStream.getAudioTracks().forEach((track) => {
        track.enabled = !track.enabled;
        setMicEnabled(toggledMic);
      });
    }
  };
  return (
    <Button
      onClick={handleToggleMic}
      variant="contained"
      style={{
        borderRadius: 50,
        marginRight: 3,
        border: "3px solid #de2500",
        backgroundColor: "transparent",
        opacity: active && permission ? 1 : 0.5,
      }}
      disabled={permission === undefined}
    >
      {!micEnabled ? (
        <img
          className="icon"
          src={require("assets/img/volume off.png").default}
        />
      ) : (
        <img
          className="icon"
          src={require("assets/img/volume up.png").default}
        />
      )}
    </Button>
  );
};

export default Microphone;
