import { api } from "api/api";
import { chainId, ethChainId } from "constants/config";

var audio = new Audio(require('assets/audio/4camsoundeffect.mp3').default);
export const getEtherPrice = async (symbol) => {
    const url = `https://api.binance.com/api/v3/ticker/price?symbol=${symbol}USDT`;
    try {
        const res = await api.get(url);
        return parseFloat(res.data.price);
    } catch (err) {
        return {
            error: true
        };
    }
}

export const convertUSDToEther = async (usdValue, chain) => {
    let ethPrice;
    if (chain === chainId) {
        ethPrice = await getEtherPrice("BNB");
    } else if (chain === ethChainId) {
        ethPrice = await getEtherPrice("ETH");
    }
    return Math.floor(usdValue/ethPrice);
}

export const playTipAlertAudio = () => {
    audio.play();
}