import { actionTypes } from "./types";

export const setLocalStream = (stream) => {
    return {
        type: actionTypes.setLocalStream,
        payload: stream,
    };
};

export const setRemoteStream = (stream) => {
    return {
        type: actionTypes.setRemoteStream,
        payload: stream,
    };
};

export const setStranger = (id) => {
    return {
        type: actionTypes.setStranger,
        payload: id,
    };
};

export const setPeerCreated = () => {
    return {
        type: actionTypes.setPeerCreated,
    };
};

export const resetVideoChat = () => {
    return {
        type: actionTypes.resetVideoChatState,
    };
};