import React, { useEffect, useState } from "react";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
// import ExtendTime from "./ExtendTime";
import SendTip from "./SendTip";
import WalletButton from "./ConnectWalletButton";
import UserCount from "./UserCount";
import { useAppSelector } from "store";
import "./header.css";

function Header() {
  return (
    <>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
        style={{ height: "8vh" }}
      >
        <Grid container item xs={4} sm={3} md={2} justifyContent="flex-start">
          <Typography
            noWrap
            style={{
              minWidth: 0,
              fontSize: "2.3rem",
              color: "#de2500",
              fontFamily: "Akira",
              cursor: "default",
            }}
          >
            4
            <span
              style={{
                marginLeft: "-1px",
                minWidth: 0,
                fontSize: "2rem",
                color: "white",
                fontFamily: "Akira",
                letterSpacing: "-1px",
                cursor: "default",
              }}
            >
              CAM
            </span>
          </Typography>
        </Grid>

        <Grid item md={3}>
          <Typography className="hd-text__do-it">
            Do it <span style={{ color: "#de2500" }}>4</span> the{" "}
            <span style={{ color: "#de2500" }}>cam</span>!
          </Typography>
        </Grid>

        {/* <Button variant="contained" onClick={onConnect} style={{marginLeft: "10px"}} >New / {online}</Button> */}
        {/* <ExtendTime /> */}

        <Grid
          item
          xs={8}
          sm={9}
          md={7}
          style={{
            display: "flex",
            alignItems: "stretch",
            justifyContent: "flex-end",
          }}
        >
          <SendTip />
          <WalletButton />
          <UserCount />
        </Grid>
      </Grid>
    </>
  );
}

export default Header;
