import { useEffect, useState } from "react";
import VideoFrame from "./VideoFrame";
import VideoPreview from "./VideoPreview";
import { Box } from "@material-ui/core";

import { store } from "store";
import { setLocalStream } from "actions/videoChatActions";
import { useWeb3React } from "@web3-react/core";
import { detectMedia } from "peer/helper";
import "./main.css";

const VideoChat = () => {
  const { active } = useWeb3React();
  const [mediaPermission, setMediaPermission] = useState({});
  const [isMediaOpen, setIsMediaOpen] = useState(false);

  const allowPermission = (stream) => {
    store.dispatch(setLocalStream(stream));
    detectMedia(function (contraints) {
      setMediaPermission({
        video: contraints.videoPermission,
        audio: contraints.audioPermission,
      });
    });
  };

  const denyPermission = (error) => {
    detectMedia(function ({ video, audio, videoPermission, audioPermission }) {
      if (!videoPermission && !audioPermission) {
      } else if (!videoPermission) {
        navigator.mediaDevices
          .getUserMedia({ audio: audio })
          .then(allowPermission);
      } else {
        navigator.mediaDevices
          .getUserMedia({ video: video })
          .then(allowPermission);
      }

      detectMedia(function (contraints) {
        setMediaPermission({
          video: contraints.videoPermission,
          audio: contraints.audioPermission,
        });
      });
    });
  };

  useEffect(() => {
    if (!active || isMediaOpen) return;
    setIsMediaOpen(true);
    detectMedia(function (contraints) {
      if (contraints.video || contraints.audio)
        navigator.mediaDevices
          .getUserMedia(contraints)
          .then(allowPermission, denyPermission);
    });
  }, [active]);

  return (
    <Box sx={{ width: "100%" }}>
      <VideoFrame mediaPermission={mediaPermission} />
      <VideoPreview />
    </Box>
  );
};

export default VideoChat;
