import { Reducer } from "redux";
import { actionTypes } from "../actions/types";

const initialState = {
    messages: [],
    isTyping: false
};

const chatReducer = (
    state = initialState,
    action
) => {
    switch (action.type) {

        case actionTypes.setMessages:
            return {
                ...state,
                messages: action.payload
            }

        case actionTypes.setTyping:
            return {
                ...state,
                isTyping: action.payload
            }

        case actionTypes.resetChat:
            return {
                ...state,
                isTyping: false,
                messages: []
            }

        default:
            return state
    }
};

export { chatReducer };
