import { store } from "../store";
import { setMessages, setTyping } from "../actions/chatActions";
import { chatRule } from "./chatRule";
import { connection } from "./peer";
import { setStrangerAddress } from "./peer";

export const detectMedia = (callback) => {
    let md = navigator.mediaDevices;
    if (!md || !md.enumerateDevices) callback({
        video: false,
        audio: false,
        videoPermission: false,
        audioPermission: false
    });
    let video = false;
    let audio = false;
    let videoPermission, audioPermission;

    md.enumerateDevices().then(devices => {
        devices.forEach(device => {
            if ( device.kind === "videoinput" ) { video = true; videoPermission = !!device.label; }
            if ( device.kind === "audioinput" ) { audio = true; audioPermission = !!device.label; }
        });
        if(/Android|iPhone|iPad/i.test(navigator.userAgent)) {
            callback ({
                video:  false, 
                audio: false,
                videoPermission: false,
                audioPermission: false
            })
        } else {
            callback ({
                video: video,
                audio: audio,
                videoPermission: videoPermission,
                audioPermission: audioPermission
            })
        }
    })
}

