export const checkAddress = (data) => {
    if (data.length === 42 && data.substring(0, 2) === "0x") return true;
    else return false;
}

export const receiveMessage = (data) => {
    if (checkAddress(data)) {
        setStrangerAddress(data)
        return;
    }
    switch(data) {
        case chatRule.startTyping: 
            store.dispatch(setTyping(true));
            break;
        case chatRule.endTyping:
            store.dispatch(setTyping(false));
            break;
        case chatRule.hello:
            break;
        default: processReceiveMsg(data)
    }
}

const processReceiveMsg = (data) => {
    let msgs = store.getState().chat.messages;
    msgs.push(
        {
            message: data,
            sentTime: new Date().getTime().toString(),
            sender: "me",
            direction: "incoming",
            position: "single"
        }
    )

    store.dispatch(setMessages(msgs));
}

export const sendMessage = (msg) => {
    try {
        connection.send(msg);
        switch(msg) {
            case chatRule.startTyping: break;
            case chatRule.endTyping: break;
            case chatRule.hello: break;
            default:
                processSendMsg(msg);
        }
        return true;
    } catch(error) {
        console.log(error);
        return false;
    }
}

const processSendMsg = (msg) => {
    if (checkAddress(msg)) {
        setStrangerAddress(msg)
        return;
    }
    let msgs = store.getState().chat.messages;
    msgs.push(
        {
            message: msg,
            sentTime: new Date().getTime().toString(),
            sender: "me",
            direction: "outgoing",
            position: "single"
        }
    )
    store.dispatch(setMessages(msgs))  
}

export const handlePeerDisconnect = async (peer) => {
    // manually close the peer connections
    let count = 0;
    try {
        for (let conns in peer.connections) {
            peer.connections[conns].forEach(async(conn, index, array) => {
                console.log(`closing ${conn.connectionId} peerConnection (${index + 1}/${array.length})`, conn.peerConnection);
                await conn.peerConnection.close();
                count++;
                // close it using peerjs methods
                if (conn.close)
                    await conn.close();
          });
        }
        return count!==0 ? true : false;
    } catch (error) {
        return false;
    }
}