import React from "react";
import { StylesProvider } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Toolbar from "@material-ui/core/Toolbar";
import HeaderBar from "../../components/Header/Header";
import FooterBar from "../../components/VideoChat/Footer";
import VideoChat from "../../components/VideoChat/VideoChat";
import { AppBar, Container, Box } from "@material-ui/core";
import Messenger from "./Messenger";
import Grid from "@material-ui/core/Grid";
import useMediaQuery from "@mui/material/useMediaQuery";
import "./main.css";

export default function Dashboard() {
  const matches = useMediaQuery("(min-width:950px)");

  return (
    <StylesProvider injectFirst>
      <CssBaseline />

        <AppBar position="static" style={{
          backgroundColor: "black",
          borderBottomLeftRadius: "22px",
          borderBottomRightRadius: "22px",
          boxShadow: "0px -4px 16px white"
        }} >
        <Toolbar>
          <HeaderBar />
        </Toolbar>
      </AppBar>

      <Grid 
        container 
        style={{
          marginTop: "3vh"
        }}
      >
        <Grid item xs={12} sm={12} md={7}>
            <Box style={{ position: "relative", height: `${matches ? "80vh" : "100%"}`, overflow: "visible"}}>
              <VideoChat />
            </Box>
        </Grid>

        <Grid item xs={12} sm={12} md={5}  style={{ marginLeft: `${matches ? "-16px" : "0px"}`, marginRight: `${matches ? "16px" : "0px"}` }}>
            <Box>
              <Messenger />
            </Box>
        </Grid>
      </Grid>

    </StylesProvider>
  );
}
