import React from "react";
import Box from "@mui/material/Box";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import { IconButton } from "@mui/material";

export default (props) => {
  const { isOpen, onRequestClose, title, children } = props;

  return (
    <Dialog
      disableEscapeKeyDown
      open={isOpen}
      onClose={onRequestClose}
      style={{ borderRadius: "18px" }}
    >
      <DialogTitle
        style={{
          backgroundColor: "#303030",
          color: "white",
        }}
      >
        {title}
        <IconButton
          style={{
            position: "absolute",
            top: "1px",
            right: "2px",
          }}
          onClick={onRequestClose}
        >
          <img
            src={require("assets/img/x_thin_icon_171414.png").default}
            style={{
              width: "18px",
              height: "18px",
            }}
          />
        </IconButton>
      </DialogTitle>
      <DialogContent style={{ backgroundColor: "#D3D3D3" }}>
        <Box component="form" sx={{ display: "flex", flexWrap: "wrap" }}>
          {children}
        </Box>
      </DialogContent>
      <DialogActions style={{ backgroundColor: "#D3D3D3" }}>
        {/* <Button onClick={onRequestClose}>Cancel</Button>
          <Button onClick={onRequestClose}>Ok</Button> */}
      </DialogActions>
    </Dialog>
  );
};
