import Web3 from 'web3';
import {
    chainId,
    ethChainId, 
    contractAddress, 
    ethContractAddress,
    ethAddress,
    rpcUrl,
    ethRpcUrl
} from "../constants/config";
import { tokenABI } from 'constants/tokenABI';
import { chatContractABI } from 'constants/chatContractABI';

var web3;
var chatContract;
var selectedContractAddress;

const getContract = (abiData, address) => {
    const abi = abiData;
    let contract = new web3.eth.Contract(abi , address);
    return contract;
}

export const init = (chain) => {
    let url;
    if (chain === chainId){
        url = rpcUrl;
        selectedContractAddress = contractAddress
    }
    else if (chain === ethChainId) {
        url = ethRpcUrl;
        selectedContractAddress = ethContractAddress
    }

    web3 = new Web3(new Web3.providers.HttpProvider(url));
    chatContract = getContract(chatContractABI, selectedContractAddress);
}

export const getEthBalance = async (address) => {
    let balance =await web3.eth.getBalance(address);
    return balance;
}

export const getTokenBalance = async (tokenAddress, address) => {
    let token = getContract(tokenABI, tokenAddress)
    let balance = await token.methods.balanceOf(address).call();
    return balance;
}

export const extendTime = async (tokenAddress, amount, address) => {
    try {
        let dataABI = chatContract.methods.extendTime(tokenAddress, amount).encodeABI();
        let txHash = await signTransaction(selectedContractAddress, address, dataABI, tokenAddress===ethAddress ? amount : 0);
        return txHash;
    } catch(error) {
        console.log(error);
        return null;
    }
}

export const sendTip = async (tokenAddress, to, amount, address) => {
    try {
        let dataABI = chatContract.methods.sendTip(tokenAddress, amount, to).encodeABI();
        let txHash = await signTransaction(selectedContractAddress, address, dataABI, tokenAddress===ethAddress ? amount : 0);
        return txHash;
    } catch(error) {
        console.log(error);
        return null;
    }
}

export const approve = async (tokenAddress, to, amount, address) => {
    try {
        let token = getContract(tokenABI, tokenAddress);
        let dataABI = token.methods.approve(to, amount).encodeABI();
        let txHash = await signTransaction(tokenAddress, address, dataABI, 0);
        return txHash;
    } catch (error) {
        console.log(error);
        return null;
    }
}

const signTransaction = async (contractAdd, address, dataABI, _etherAmount) => {
    const transactionParameters = {
        to: contractAdd, // Required except during contract publications.
        from: address, // must match user's active address.
        data: dataABI,
        gasLimit: "0x5208",
        chainId: chainId,
        value: parseInt(_etherAmount).toString(16),  // this should be hex !!!!
      };
    
    //sign the transaction
    try {
        const txHash = await window.ethereum.request({
          method: "eth_sendTransaction",
          params: [transactionParameters],
        });
        return await checkTx(txHash);
    } catch (error) {
        console.log(error);
        return null;
    }
}

const checkTx = async (txHash) => {
    // let result = await web3.eth.getTransaction(txHash)
    let result = null;
    while(result==null) {
        result = await web3.eth.getTransactionReceipt(txHash.toString());
    }
    if (result.status) return txHash;
    else return null;
}