import Peer from "peerjs";
import { store } from "../store";
import { 
    setLocalStream, 
    setRemoteStream, 
} from "../actions/videoChatActions";
import { setConnected, setHeaderMessage, setRequestAccepted, setRTCServerConnected } from "../actions/globalActions";
import { resetChat } from "../actions/chatActions";
import { resetVideoChat } from "../actions/videoChatActions";
import { emitMatched, disconnectSocketServer } from "./socket";
import { 
    detectMedia,
    receiveMessage,
    sendMessage,
} from "./helper";

export var peer = null;
export var connection = null;
export var address = null;
export var strangerAddress = null;
var outgoing = null;
var peerID = "";

const sleep = (milliseconds) => {
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
};

export const setAddress = ( account ) => {
    address = account;
}

export const setStrangerAddress = ( account ) => {
    strangerAddress = account;
}

export const createPeer = (socketId) => {
    try {
        _createPeer(socketId);
    } catch (error) {
        console.log(error);
    }
}

export const _createPeer = (socketId) => {
    console.log(socketId, typeof(socketId))
    peer = null;
    peerID = socketId;
    peer = new Peer(("a"+socketId.replace(/-|_/g, "")), {
        // secure: true, 
        host:"chadinuchat.hopto.org",
        // host:"192.168.8.184",
        port: 9000,
        pingInterval: 25000,
        // config: {'iceServers': [
        //     {
        //         'urls': 'stun:stun.l.google.com:19302'
        //     },
        //     {
        //         'urls': 'turn:192.158.29.39:3478?transport=udp',
        //         'credential': 'JZEOEt2V3Qb0y27GRntt2u2PAYA=',
        //         'username': '28224511:1379330808'
        //     },
        //     {
        //         'urls': 'turn:192.158.29.39:3478?transport=tcp',
        //         'credential': 'JZEOEt2V3Qb0y27GRntt2u2PAYA=',
        //         'username': '28224511:1379330808'
        //     }
        // ]},
        debug: 1
    });

    peer.on("open", () => {
        console.log("connect to peer server successfully")
        store.dispatch(setRTCServerConnected(true));
    })

    peer.on('connection', (conn) => {
        console.log('incoming peer connection!', conn);
        conn.on('open', () => {
            emitMatched({ peerId: conn.metadata.id });
            sendMessage(address);
        });
        conn.on('data', (data) => {
            if (!store.getState().global.connected)
                store.dispatch(setConnected(true));
            receiveMessage(data);
        });
        conn.on("close", ()=>{
            console.log("connection closed")
            // disconnectSocketServer(); //disconnect socket
            // closePeer();
        })
        connection = conn;
    });
    
    peer.on("call", (call) => {
        call.on("stream", (comingStream) => {
            store.dispatch(setRemoteStream(comingStream));
        });
        try {
            let localStream = store.getState().videoChat.localStream;
            // detectMedia(function (contraints) {
            //     if (contraints.video || contraints.audio)
            //         navigator.mediaDevices
            //             .getUserMedia(contraints)
            //             .then(stream => {
            //                 call.answer(stream);
            //                 store.dispatch(setLocalStream(stream));
            //             });
            // })
            if (!!localStream) call.answer(localStream);
            else call.answer();
        } catch (error) {
            console.log(error);
        }
    });

    peer.on("close", () => {
        console.log("connection close");
        // disconnectSocketServer(); //disconnect socket
        // alert("Your match close.")
        // closePeer(); //disconnect socket
    })

    peer.on("disconnected", () => {
        console.log("disconnected from peer server");
        // disconnectSocketServer(); //disconnect socket
        // alert("Your match disconnected.")
        // closePeer() //disconnect socket
        peer.reconnect();
    })
}

export const onCall = async (destination) => {
    try {
        await _onCall(destination);
    } catch (error) {
        console.log(error)
    }
}

export const _onCall = async (destination) => {
    try{
        connection = peer.connect(("a"+destination.replace(/-|_/g, "")), {metadata: {id: peerID}});
        connection.on('open', function() {
            emitMatched({ peerId: destination });
            sendMessage(address);

        });
        connection.on('data', (data) => { 
            if (!store.getState().global.connected)
                store.dispatch(setConnected(true));
            receiveMessage(data);
        });
        connection.on("close", ()=>{
            console.log("connection close")
            // disconnectSocketServer(); //disconnect socket
            // alert("Your match disconnected. close")
            // closePeer() //disconnect socket
        });

        let localStream = store.getState().videoChat.localStream;
        if (!!localStream) {
        // // Media stream connection
        // detectMedia(function(contraints) {
        //     if (contraints.video || contraints.audio)
        //         navigator.mediaDevices
        //             .getUserMedia(contraints)
        //             .then(stream => {
                        outgoing = peer.call(("a"+destination.replace(/-|_/g, "")), localStream);
                        // store.dispatch(setLocalStream(stream));

                        outgoing.on("stream", (comingStream) => {
                            store.dispatch(setRemoteStream(comingStream));
                        });
                // });
                    }
            else {
                outgoing = peer.call(("a"+destination.replace(/-|_/g, "")), new MediaStream()); 
                outgoing.on("stream", (comingStream) => {
                    store.dispatch(setRemoteStream(comingStream));
                });
            }
        // })
    } catch(error) {
        console.log(error)
    }
}

export const closePeer = async () => {
    store.dispatch(resetChat()); // reset messages
    store.dispatch(setRemoteStream(null)); // reset video
    // set connected false
    store.dispatch(setConnected(false));
    store.dispatch(setRTCServerConnected(false));
    store.dispatch(setHeaderMessage("Disconnected, click new!"));

    disconnectSocketServer(); //disconnect socket
    // resumeRequest()
    // await sleep(2000);

    console.log(peer);
    
    try {
        if (peer !== null) {
            await peer.destroy();
            peer = null;
        }

        if (connection !== null) {
            await connection.close();
            connection = null;
        }

        if (outgoing !== null) {
            console.log(outgoing, "outgoing");
            outgoing = null;
        }
    } catch(error) {
        console.log(error);
    }
}