import { Reducer } from "redux";
import { actionTypes } from "../actions/types";

const initialState = {
    duration: 200,
    online: 0,
    connected: false,
    waiting: false,
    headerMessage: "Connect your wallet!",
    isRTCServerConnected: false,
    isRequestAccepted: false,
    receivedTipAmount: 0
};

const globalReducer = (
    state = initialState,
    action
) => {
    switch (action.type) {

        case actionTypes.setRemainedTime:
            return {
                ...state,
                duration: action.payload
            }
        
        case actionTypes.initializeRemainedTime:
            return {
                ...state,
                duration: 0
            }

        case actionTypes.setOnline:
            return {
                ...state,
                online: action.payload
            }
        
        case actionTypes.setConnected:
            return {
                ...state,
                connected: action.payload,
                waiting: false
            }
        
        case actionTypes.setWaiting:
            return {
                ...state,
                waiting: action.payload
            }
        case actionTypes.setHeaderMessage:
            return {
                ...state,
                headerMessage: action.payload
            }

        case actionTypes.setRTCServerConnected:
            return {
                ...state,
                isRTCServerConnected: action.payload
            }

        case actionTypes.setRequestAccepted:
            return {
                ...state,
                isRequestAccepted: action.payload
            }

        case actionTypes.setReceivedTipAmount:
            return {
                ...state,
                receivedTipAmount: action.payload
            }
        
        default:
            return state
    }
};

export { globalReducer };
