import axios from "axios";

const BASE_URL = "https://chadinuchat.hopto.org:5050/";
// const BASE_URL = "https://fourcam.onrender.com:80/";
// const BASE_URL = "http://192.168.8.184:5050/";

export const api = axios.create({
    baseURL: BASE_URL
});

