import { actionTypes } from "./types";

export const setMessages = (messages) => {
    return {
        type: actionTypes.setMessages,
        payload: messages,
    };
};


export const setTyping = (typing) => {
    return {
        type: actionTypes.setTyping,
        payload: typing
    };
};

export const resetChat = () => {
    return {
        type: actionTypes.resetChat,
    };
};

