import React, {useEffect, useLayoutEffect, useCallback, memo, useMemo} from 'react'
import { useWalletModal, dark} from '@pancakeswap-libs/uikit'
import { ModalProvider } from "@pancakeswap-libs/uikit";
import { ThemeProvider } from "styled-components";
import { injectStyle } from "react-toastify/dist/inject-style";
import { Button } from "@material-ui/core";

import { useWeb3React, UnsupportedChainIdError } from '@web3-react/core'
import {
  NoEthereumProviderError,
  UserRejectedRequestError as UserRejectedRequestErrorInjected
} from '@web3-react/injected-connector'
import { URI_AVAILABLE, UserRejectedRequestError as UserRejectedRequestErrorWalletConnect } from '@web3-react/walletconnect-connector'
import { toBech32 } from '@harmony-js/crypto'

import useEagerConnect from 'hooks/useEagerConnect'
import useInactiveListener from 'hooks/useInactiveListener'
import { setAddress } from 'peer/peer';

import {
  injected,
  network,
  walletconnect,
  walletlink
} from './connectors'


const connectorsByName = {
  injected: injected,
  network: network,
  walletconnect: walletconnect,
  bsc: walletlink,
}


function Account() {
  var { account, library } = useWeb3React()
  const isHmyLibrary = (library?.messenger?.chainType === 'hmy')
  account = (isHmyLibrary && account) ? toBech32(account) : account

  return (
    <>
      <Button 
        variant="outlinedSecondary"
        className='narrow-line-height'
        style={{color: "white", fontWeight: "700", fontSize: "1rem", zIndex: "1000", opacity: "0.5"}}
      >
        {account === null
          ? '-'
          : account
          ? `${account.substring(0, 6)}...${account.substring(account.length - 4)}`
          : ''}
      </Button>
    </>
  )
}

const theme = dark;
theme.colors.text = "#ffffff";
theme.card.background = "#777";
theme.card.cardHeaderBackground = {
  default: "goldenrod",
  blue: "aquamarine",
  violet: "coral"
};

function ConnectButton() {
  
  const context = useWeb3React();
  const { connector, account, activate, deactivate, active, error } = context
  
  // handle logic to recognize the connector currently being activated
  const [activatingConnector, setActivatingConnector] = React.useState(null)
  
  const { onPresentConnectModal } = useWalletModal(
    (name) => {
      try {
        const currentConnector = connectorsByName[name]
        setActivatingConnector(currentConnector)
        activate(connectorsByName[name])
        // console.log("------------------", name, currentConnector);
      } catch (error) {
        console.log(error);
      }
    }, 
    ()=>{
      // onDisconnectClick()
    }
  )

  const getErrorMessage = useCallback(() => {
    if (error instanceof NoEthereumProviderError) {
      console.log( 'No Ethereum browser extension detected, install MetaMask on desktop or visit from a dApp browser on mobile.');
    } else if (error instanceof UnsupportedChainIdError) {
      console.log( "You're connected to an unsupported network.");
    } else if (
      error instanceof UserRejectedRequestErrorInjected ||
      error instanceof UserRejectedRequestErrorWalletConnect||
      (typeof error == 'object'&&error.toString().includes("User closed modal"))
      // error instanceof UserRejectedRequestErrorFrame
    ) {
      if (connector===walletconnect) {
        connector.walletConnectProvider = null;
      }
        console.log( 'Please authorize this website to access your Ethereum account.');
    } else {
      console.log( 'An unknown error occurred. Check the console for more details.');
    }
  }, [error]);

  React.useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  // handle logic to eagerly connect to the injected ethereum provider, if it exists and has granted access already
  const triedEager = useEagerConnect()

  // handle logic to connect in reaction to certain events on the injected ethereum provider, if it exists
  useInactiveListener(!triedEager || !!activatingConnector)
  
  // log the walletconnect URI
  useEffect(() => {
    const logURI = uri => {
      // console.log("WalletConnect URI", uri);
    };
    walletconnect.on(URI_AVAILABLE, logURI);

    injectStyle();

    return () => {
      walletconnect.off(URI_AVAILABLE, logURI);
    };
  }, []);

  useEffect(() => {
    if (context.error!==undefined)
    {
      getErrorMessage(context.error);
    }
  }, [context.error])

  useEffect(() => {
    setAddress( context.account );
  }, [context.account])

  // const onDisconnectClick = () => {
  //   deactivate();
  //   if(connector===walletconnect)
  //     connector.walletConnectProvider=null;
  // }

  return (
    <>
      {!active ? 
        <Button 
          variant="outlinedSecondary"
          className='narrow-line-height'
          style={{color: "white", fontWeight: "700", fontSize: "0.97rem", zIndex: "10", lineHeight: "0.9"}}
          onClick={onPresentConnectModal} 
        >Connect<br />Wallet</Button> : 
        <Account />
      }
    </>
  )
}

function WalletButton() {
  return (
    <ThemeProvider theme={theme}>
      <ModalProvider>
        <ConnectButton />
      </ModalProvider>
    </ThemeProvider>
  )
}

export default WalletButton;