import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { Button } from "@material-ui/core";
import { Box } from "@material-ui/core";

import Video from "./Video";
import Microphone from "./Microphone";
import Camera from "./Camera";
import GuidFrame from "./GuideFrame";
import { closePeer } from "../../peer/peer";
import { connectWithSocketServer } from "../../peer/socket";
import { restart } from "api/api";
import { useAppSelector } from "../../store";
import { useWeb3React } from "@web3-react/core";
import { store } from "../../store";
import { setHeaderMessage } from "actions/globalActions";
import { socket } from "../../peer/socket";

const styles = ({ breakpoints }) => ({
  root: {
    padding: 0,
    [breakpoints.up("sm")]: {
      padding: 0,
      maxWidth: 600,
      margin: "auto",
    },
    [breakpoints.up("md")]: {
      maxWidth: "95%",
    },
  },
  mainGrid: {
    minHeight: 380,
    padding: 0,
  },
});

const ContentForm = ({ classes, mediaPermission }) => {
  const { localStream, remoteStream } = useAppSelector(
    (state) => state.videoChat
  );
  const { connected, headerMessage, isRTCServerConnected, isRequestAccepted } =
    useAppSelector((state) => state.global);

  const [timestamp, setTimestamp] = useState(0);

  const [endClicked, setEndClicked] = useState(false);
  const [bgNEWColor, setBgNEWColor] = useState("black");
  var { active } = useWeb3React();

  const sleep = (milliseconds) => {
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
  };

  const onConnect = async () => {
    let current = new Date().getTime();
    if (current - timestamp < 2000) return;
    if (!active) return;
    await closePeer(); // initialze pervious one
    if (headerMessage === "Waiting for a match...") {
      store.dispatch(setHeaderMessage("Click New To Start!"));
      return;
    }
    // set waiting true
    store.dispatch(setHeaderMessage("Waiting for a match..."));
    // connect to socket server
    setTimestamp(current);
    connectWithSocketServer();
  };

  const connectWithPeer = async () => {
    let isStrangerExisting = false;
    while (
      isRTCServerConnected &&
      !isStrangerExisting &&
      !store.getState().global.connected &&
      !!socket
    ) {
      console.log("request");
      let stranger = await restart(socket.id);
      if (stranger.id !== socket.id && stranger.id !== undefined) {
        try {
          isStrangerExisting = true;
          socket.emit("request-new-match", { peerId: stranger.id });
          break;
        } catch (error) {
          console.log(error);
          break;
        }
      }
      await sleep(1000);
    }
  };

  useEffect(() => {
    if (isRTCServerConnected) {
      try {
        connectWithPeer();
      } catch (error) {
        console.log(error);
      }
    }
  }, [isRTCServerConnected]);

  const onDisconnect = async (event) => {
    event.preventDefault();
    if (!endClicked) {
      setEndClicked(true);
      return;
    }
    setEndClicked(false);
    await closePeer();
  };

  const cancelEndClick = (e) => {
    if (e.defaultPrevented) return;
    setEndClicked(false);
  };

  useEffect(() => {
    document.body.addEventListener("click", cancelEndClick);

    return function cleanup() {
      window.removeEventListener("click", cancelEndClick);
    };
  }, []);

  useEffect(() => {
    if (connected) setBgNEWColor("#a9a9a97a");
    else {
      setEndClicked(false);
      if (headerMessage === "Waiting for a match...") {
        setBgNEWColor("#302d2d");
      } else setBgNEWColor("black");
    }
  }, [connected, active, headerMessage]);

  return (
    <div className={classes.root}>
      <Paper
        elevation={1}
        square
        style={{
          border: "1px solid transparent",
          borderRadius: "10px",
          backgroundColor: "#101010",
          boxShadow: "1px 0px 10px white",
          width: "100%",
        }}
      >
        <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
          className={classes.mainGrid}
        >
          <Grid
            container
            item
            xs={12}
            justifyContent="center"
            alignItems="center"
          >
            <Box id="video">
              {!connected && <GuidFrame />}
              {remoteStream && (
                <Video stream={remoteStream} isLocalStream={false} />
              )}
            </Box>
          </Grid>

          <Grid
            container
            item
            xs={12}
            justifyContent="center"
            alignItems="center"
            className="video-icon-group"
          >
            <Microphone
              localStream={localStream}
              permission={mediaPermission.audio}
            />
            <Button
              variant="contained"
              style={{
                borderRadius: 50,
                color: "#fff",
                border: "3px solid",
                borderColor: !active ? "grey" : "white",
                backgroundColor: bgNEWColor,
              }}
              // disabled={headerMessage === "Waiting for a match..."}
              onClick={!connected ? onConnect : onDisconnect}
            >
              <div
                style={{
                  fontSize: "1.4rem",
                  lineHeight: "1.25",
                  fontWeight: "bold",
                  color: !active ? "grey" : "white",
                  cursor: active ? "pointer" : "default",
                }}
              >
                {!connected ? "NEW" : endClicked ? "REALLY?" : "END"}
              </div>
            </Button>
            <Camera
              localStream={localStream}
              permission={mediaPermission.video}
            />
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
};

export default withStyles(styles)(ContentForm);
