import React, { useState, useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import { Button } from "@material-ui/core";
import { toast } from "react-toastify";

const Camera = ({ localStream, permission }) => {
  const [cameraEnabled, setCameraEnabled] = useState(true);
  var { active } = useWeb3React();

  const handleToggleCamera = () => {
    if (permission === false)
      toast.success(
        "Allow cam permission in your browser settings to toggle on/off."
      );
    try {
      let newStatus = !cameraEnabled;
      localStream.getVideoTracks().forEach((track) => {
        track.enabled = !track.enabled;
        setCameraEnabled(newStatus);
      });
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (localStream === null) {
      setCameraEnabled(true);
    }
  }, [localStream]);

  return (
    <Button
      onClick={handleToggleCamera}
      variant="contained"
      style={{
        borderRadius: 50,
        marginLeft: 3,
        border: "3px solid #de2500",
        backgroundColor: "transparent",
        opacity: active && permission ? 1 : 0.5,
      }}
      disabled={permission === undefined}
    >
      {!cameraEnabled ? (
        <img
          className="icon"
          src={require("assets/img/camera off.png").default}
        />
      ) : (
        <img
          className="icon"
          src={require("assets/img/camera on.png").default}
        />
      )}
    </Button>
  );
};

export default Camera;
