import { actionTypes } from "./types";
import { store } from "store";

export const setRemainedTime = (duration) => {
    return {
        type: actionTypes.setRemainedTime,
        payload: store.getState().global.duration + duration,
    };
};

export const initializeRemainedTime = () => {
    return {
        type: actionTypes.initializeRemainedTime,
    };
};

export const setOnline = (onlineUsers) => {
    return {
        type: actionTypes.setOnline,
        payload: onlineUsers,
    };
};

export const setWaiting = (isWaiting) => {
    return {
        type: actionTypes.setWaiting,
        payload: isWaiting
    };
};

export const setHeaderMessage = (msg) => {
    return {
        type: actionTypes.setHeaderMessage,
        payload: msg
    };
};

export const setConnected = (isConnected) => {
    return {
        type: actionTypes.setConnected,
        payload: isConnected,
    };
};

export const setRTCServerConnected = ( isRTCServerConnected ) => {
    return {
        type: actionTypes.setRTCServerConnected,
        payload: isRTCServerConnected,
    };
};

export const setRequestAccepted = ( isRequestAccepted ) => {
    return {
        type: actionTypes.setRequestAccepted,
        payload: isRequestAccepted,
    };
};

export const setReceivedTipAmount = ( receivedTipAmount ) => {
    return {
        type: actionTypes.setReceivedTipAmount,
        payload: receivedTipAmount,
    };
};