import * as React from "react";
import { useTheme } from "@mui/material/styles";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import { useWeb3React } from "@web3-react/core";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { currenices } from "constants/config";
import "./token-list.css";

const ITEM_HEIGHT = 58;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: { md: 300, xs: 200 },
    },
  },
};

export default function Tokenlist(props) {
  const { selectToken } = props;
  var { chainId } = useWeb3React();
  let tokens = currenices[chainId];
  const [selectedToken, setSelectedToken] = React.useState(
    !!tokens ? tokens[0] : ""
  );

  const handleChange = (event) => {
    const {
      target: { value },
    } = event;
    tokens.forEach((element, index) => {
      if (element.name === value) {
        setSelectedToken(tokens[index]);
        selectToken(tokens[index]);
      }
    });
  };

  return (
    <div>
      <FormControl sx={{ m: 1, width: { xs: 200, md: 300 } }}>
        <InputLabel
          id="demo-multiple-name-label"
          style={{ fontSize: "1.2rem" }}
        >
          Cryptocurrency
        </InputLabel>
        <Select
          labelId="demo-multiple-name-label"
          id="demo-multiple-name"
          value={selectedToken.name}
          onChange={handleChange}
          input={<OutlinedInput label="Select CryptoCurrency" />}
          MenuProps={MenuProps}
        >
          {!!tokens &&
            tokens.map((token) => (
              <MenuItem
                key={token.name}
                value={token.name}
                style={{ fontFamily: "Impact" }}
              >
                <img
                  style={{
                    width: "32px",
                    height: "32px",
                    marginRight: "10px",
                  }}
                  src={token.imageUrl}
                  alt=""
                />
                {token.name}
              </MenuItem>
            ))}
        </Select>
      </FormControl>
    </div>
  );
}
