import React, { useState, useEffect } from "react";
import FullscreenIcon from "@material-ui/icons/Fullscreen";
import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";
import IconButton from "@material-ui/core/IconButton";
import { useAppSelector } from "../../store";
import useMediaQuery from "@mui/material/useMediaQuery";
import Video from "./Video";

function VideoPreview() {
  const matches = useMediaQuery("(min-width:600px)");
  const [hidden, toggleHidden] = useState(false);
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  const [full, toggleFull] = useState(false);
  const { localStream } = useAppSelector((state) => state.videoChat);
  const [preViewSize, setPreViewSize] = useState(0);
  const [minPreViewSize, setMinPreViewSize] = useState(30);

  function minimize() {
    toggleHidden(!hidden);
    setHeight(height === minPreViewSize ? preViewSize : minPreViewSize);
    setWidth(preViewSize);
    toggleFull(true);
  }
  function maximize() {
    if (full) {
      setWidth("80%");
      setHeight("80%");
    } else {
      setWidth(preViewSize);
      setHeight(preViewSize);
    }
    toggleFull(!full);
  }

  useEffect(() => {
    let _preViewSize = matches ? 250 : 150;
    setMinPreViewSize(matches ? 30 : 24);
    setPreViewSize(_preViewSize);
    setWidth(_preViewSize);
    setHeight(_preViewSize);
  }, [matches]);

  return (
    <div
      className="video-preview"
      style={{
        color: "#fff",
        textAlign: "right",
        background: "#272727",
        position: "absolute",
        borderRadius: 5,
        transition: "ease-in 0.3s all",
        width: width,
        height: height,
        bottom: "-5px",
        left: "16px",
      }}
    >
      {localStream && <Video stream={localStream} isLocalStream={true} />}

      <IconButton
        onClick={maximize}
        size="small"
        style={{
          position: "absolute",
          bottom: 0,
          left: 0,
          color: "#fff",
        }}
      >
        <FullscreenIcon />
      </IconButton>
      <IconButton
        onClick={minimize}
        size="small"
        style={{
          position: "absolute",
          top: 0,
          right: 0,
          color: "#fff",
        }}
      >
        {height !== minPreViewSize ? <RemoveIcon /> : <AddIcon />}
      </IconButton>
    </div>
  );
}
export default VideoPreview;
