import React from "react";
// import Typography from "@material-ui/core/Typography";
import { Box } from "@mui/material";
// import useMediaQuery from "@mui/material/useMediaQuery";
import { useWeb3React } from "@web3-react/core";

const GuidFrame = () => {
  // const matches = useMediaQuery("(min-width:600px)");
  var { active } = useWeb3React();

  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        color: "white",
        fontSize: "1.5rem",
      }}
    >
      <Box sx={{ marginTop: "20%", display: "flex", justifyContent: "center" }}>
        <Box>
          {/* <Typography
            style={{
              marginBottom: "0.8rem",
              fontSize: "1.6rem",
              width: "80%",
              marginLeft: "10%",
            }}
          >
            1. Connect a wallet to activate the 4cam web3 experience!🔥
            <br />
          </Typography>
          <Typography
            style={{
              marginBottom: "0.8rem",
              fontSize: "1.6rem",
              width: "80%",
              marginLeft: "10%",
            }}
          >
            2. {matches ? "Click" : "Tap"} NEW to chat, and allow mic/cam
            permissions if you’d like matches to see/hear you
          </Typography> */}
          <img
            src={
              require(!active
                ? "assets/img/guid_1.png"
                : "assets/img/guid_2.png").default
            }
            style={{ width: "88%", margin: "auto 6%" }}
            alt=""
          />
        </Box>
      </Box>
    </div>
  );
};

GuidFrame.propTypes = {};
GuidFrame.defaultProps = {};

export default GuidFrame;
