import React, { useEffect, useState } from "react";
import { useAppSelector } from "store";

function UserCount() {
  const { online } = useAppSelector((state) => state.global);
  useEffect(() => {}, []);

  return (
    <div className="hd-text__user-count">
      {/* <span
        style={{
          fontSize: "2rem",
          fontWeight: "bold"
        }}
      >
        1,000+&nbsp;
      </span>
      <span>online now</span> */}
      <img
        src={require("assets/img/win_300.png").default}
        style={{ height: "4vh" }}
      />
    </div>
  );
}

export default UserCount;
