import React, { useState, useEffect } from "react";
import styles from "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import {
  MainContainer,
  ChatContainer,
  MessageList,
  Message,
  MessageInput,
  Avatar,
  ConversationHeader,
  InfoButton,
  TypingIndicator,
  MessageSeparator,
} from "@chatscope/chat-ui-kit-react";
import { useAppSelector } from "../../store";
import useMediaQuery from "@mui/material/useMediaQuery";
import { sendMessage } from "../../peer/helper";
import { chatRule } from "../../peer/chatRule";
import { connection } from "../../peer/peer";
// import { stopRequest } from "peer/socket";
import { useWeb3React } from "@web3-react/core";
import { store } from "../../store";
import { setHeaderMessage } from "actions/globalActions";
import { toast } from "react-toastify";
import "./messenger.css";

export default function Messenger() {
  const matches = useMediaQuery("(min-width:600px)");
  const { active } = useWeb3React();
  const [messageInputValue, setMessageInputValue] = useState("");
  const [focused, setFocused] = useState(false);
  
  const onFocus = () => setFocused(true);
  const onBlur = () => setFocused(false);

  const { messages, isTyping } =
    useAppSelector((state) => state.chat);
  const { connected, receivedTipAmount, headerMessage } =
    useAppSelector((state) => state.global);

  const sleep = (milliseconds) => {
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
  };

  const sendMsgToStranger = () => {
    if ( messageInputValue.length > 0 && connection!==null && connected ){
      let success = sendMessage(messageInputValue);
      if (success) {
        setMessageInputValue("")
      } else {
        toast.success("Connection Error");
      }
    }
  }

  const getTimeString = () => {
    let date = new Date();
    let ap = "AM";
    let hour = date.getHours();
    if (hour >= 12 ) {
      ap="PM";
      if (hour > 12) hour-= 12;
    }
    return (
      date.toDateString() + " " + 
      hour + ":" + 
      (date.getMinutes() < 10 ? "0" : "") + date.getMinutes() + " " + 
      ap
    );
  }

  // useEffect(() => {
    
  //     setInterval(() => {

  //       let prevMessageValue = "";
      
  //       if ( connection === null ) return;
    
  //       if (prevMessageValue !== messageInputValue)
  //         sendMessage(chatRule.startTyping);
  //       else
  //         sendMessage(chatRule.endTyping);
  //       console.log(prevMessageValue, messageInputValue);
  //       prevMessageValue = messageInputValue;
  //     }, 3000)

  // }, [])

  useEffect(() => {
    if (!!receivedTipAmount) toast.success(`You got a $${receivedTipAmount} tip!`);
  }, [receivedTipAmount])

  useEffect(() => {
    let notifyTypingMsg;
    if ( focused && !!messageInputValue )
      notifyTypingMsg = chatRule.startTyping;
    else notifyTypingMsg = chatRule.endTyping;

    if (connection !== null)
      sendMessage(notifyTypingMsg);
  }, [focused, messageInputValue]);

  useEffect(() => {
    if (connected) {
      // stopRequest()
      store.dispatch(setHeaderMessage("Now connected!"))
    }
  }, [connected])

  useEffect(() => {
    if (active && !connected) store.dispatch(setHeaderMessage("Click new to start!"))
    else if (!connected) store.dispatch(setHeaderMessage("Connect your wallet!"))
  }, [active])

  return (
    <MainContainer responsive
      style={{
        height: `${matches ? "80.3vh" : "30vh"}`,
        minHeight: "400px",
        position: "relative",
        zIndex: "0",
        borderRadius: "10px", border: "1px solid"
      }}
    >
      <ChatContainer>
        <ConversationHeader style={{backgroundColor: "#272727"}}>
          <ConversationHeader.Back />
          <Avatar src={require("assets/img/icon art.png").default} name="Stranger" />
          <ConversationHeader.Content
            style={{backgroundColor: "#272727", fontFamily: "Akira"}}
            userName={ headerMessage }
          />
          <ConversationHeader.Actions>
            <InfoButton />
          </ConversationHeader.Actions>
        </ConversationHeader>
        <MessageList
          style={{backgroundColor: "#272727"}}
          typingIndicator={isTyping?<TypingIndicator style={{backgroundColor: "#272727"}} content="Stranger is typing" /> : null}
        >
          <MessageSeparator style={{backgroundColor: "#272727", color: "white"}} content={getTimeString()} />
          {messages.length>0 && messages.map(msg => (
            <Message 
              model={msg}
              key={msg.sentTime}
            />
          ))}
        </MessageList>
        <MessageInput
          placeholder="Type your message..."
          attachButton={false}
          value={connected ? messageInputValue : ""}
          onChange={(val) => setMessageInputValue(val)}
          onSend={sendMsgToStranger}
          onFocus={onFocus}
          onBlur={onBlur}
          style={{backgroundColor: "#272727"}}
          disabled={!connected}
        />
      </ChatContainer>
    </MainContainer>
  );
}
